/* ==========================================================================
   Header 
   ========================================================================== */
header {
  
}

.home header {
  background-color: #e6e5d8;
}

header .wrapper {
  height: 112px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  width: 170px;
}
.header__logo img {
  width: 100%;
}
.header__nav {
  list-style: none;
  margin: 0px;
  padding: 0;
  display: flex;  
}
.header__nav > li {
  margin-right: 25px;
}
.header__nav > li:last-child {
  margin-right: 0;
}
.header__nav > li a {
  display: inline-block;
  padding: 0 17px;
  position: relative;
  font-family: LucidaGrande;
  font-size: 16px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #4c5c4f;
}
.header__nav > li.current a {
  color: $orange;
}
.header__nav > li a::after {
  content: "";
  display: block;
  width: 0%;
  height: 3px;
  background-color: $orange;
  position: absolute;  
  left: 0;
  bottom: -10px;
  transition: all 250ms ease;
}
.header__nav > li:hover a::after {
  width: 100%;  
}
.header__nav > li.current a::after {
  width: 100%;
}

.header__tocontacts {

}
.header__tocontacts a {
  display: inline-flex;
  width: 170px;
  height: 54px;
  justify-content: center;
  align-items: center;
  background-color: rgba(230, 121, 44, 1);
  font-family: LucidaGrande;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #ffffff;
  transition: all 300ms ease;  
  position: relative;
    &::before {
      content: attr(data-text);
      z-index: 1;
    }
    &::after {
      content: "";
      display: block;
      width: 0%;
      height: 100%;
      background-color: $buttonHover;
      z-index: 0;
      position: absolute;
      top: 0;
      left: 0;
      transition: all 300ms ease;
    }
    &:hover::after {
      width: 100%;
    }
}



/* ==========================================================================
   Home
   ========================================================================== */
.home__main-slider {  
  padding-top: 25px;
  background-color: #e6e5d8;
  height: 790px;
}
.home__main-slider_slide {
  display: flex;
  align-items: flex-start;  
  position: relative;
}
.home__main-slider_slide .slide--left {
  width: 695px;
  padding: 218px 0px 153px 0px;
  
}
.home__main-slider_slide .swiper-slide {
  padding-left: 97px;
}
.home__main-slider_slide .orange-title {
  font-family: LucidaGrande;
  font-size: 18px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.89;
  letter-spacing: normal;
  color: $orange;
  margin-bottom: 11px;
}
.home__main-slider_slide .black-title {
  font-family: Helvetica;
  font-size: 48px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.35;
  letter-spacing: normal;
  color: #000000;
  margin-bottom: 34px;
}
.home__main-slider_slide .description {
  font-family: Hind;
  font-size: 18px;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.89;
  letter-spacing: normal;
  color: #4c5c4f;
  margin-bottom: 30px;
}
.home__main-slider_slide .more a {
  display: inline-block;
  font-family: LucidaGrande;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: $orange;
  padding-right: 37px;
  position: relative;
  transition: all 250ms ease;
}
.home__main-slider_slide .more a::before {
  content: "";
  display: block;
  height: 1px;
  width: 21px;
  background-color: $orange;
  position: absolute;
  right: 4px;
  top: calc(50% - 1px);
  transition: all 250ms ease;
}
.home__main-slider_slide .more a::after {
  content: "";
  display: block;
  width: 0;
  height: 0;
  border-top: 3.5px solid transparent;
  border-left: 4px solid $orange;
  border-bottom: 3.5px solid transparent;
  position: absolute;
  right: 0;
  top: calc(50% - 4px);
  transition: all 250ms ease;
}
.home__main-slider_slide .more a:hover {
  /*color: #4c5c4f;*/
}
.home__main-slider_slide .more a:hover:before {  
  /*background-color: #4c5c4f;*/
  width: 31px;
  right: -6px;
}
.home__main-slider_slide .more a:hover:after {  
  /*border-left-color: #4c5c4f;*/
  right: -10px;
}

.home__main-slider_slide .slide--right {
  position: relative;
  flex-grow: 1;  
  text-align: right;
  background-color: #e6e5d8;
}

.home__main-slider_slide .slide--right .video {
  width: 905px;
  height: 905px;
  margin-left: auto;
  position: relative;
}
.home__main-slider_slide .slide--right .video::before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  opacity: 0.77;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.02) 0%, #000000);
  position: absolute;
  top: 0;
  left: 0;
  transition: all 150ms ease;
}
.home__main-slider_slide .slide--right .video::after {
  content: "";
  display: block;
  width: 136px;
  height: 136px;
  background: url('../img/video-play.svg') no-repeat center / cover;
  position: absolute;
  top: calc(50% - 68px);
  left: calc(50% - 68px);
  transition: all 250ms ease;
  cursor: pointer;
}
.home__main-slider_slide .slide--right .video:hover::before {
  opacity: 1;
}
.home__main-slider_slide .slide--right .video:hover::after {
  transform: scale(1.1);
}

.home__main-slider_slide .slide--right .video img {
  width: 100%;
}
.home__main-slider_navigation {
  position: absolute;
  top: 87px;
  left: 97px;
  z-index: 1;  
  font-family: LucidaGrande;
  font-size: 12px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: $orange;
}
.home__main-slider_navigation .swiper-pagination-current {
  font-family: Helvetica;
  font-size: 32px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #000000;
  display: inline-block;
  margin-right: -2px;

}
.home__main-slider_navigation .swiper-pagination-total {
  display: inline-block;
  margin-left: -3px;
}
.home__main-slider_navigation--arrows {
  margin-top: 17px;
  display: flex;
  justify-content: space-between;
  width: 100px;
}
.home__main-slider_navigation--arrows .arrow-prev {
  width: 28px;
  height: 24px;
  position: relative;  
  overflow: hidden;
  transition: all 200ms ease;  
  cursor: pointer;
}
.home__main-slider_navigation--arrows .arrow-prev::before {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  background-color: $orange;
  position: absolute;
  left: 0;
  top: calc(50% - 1px);
  transition: all 300ms ease;
}
.home__main-slider_navigation--arrows .arrow-prev::after {
  content: "";
  display: block;
  width: 0;
  height: 0;
  border-top: 4px solid transparent;
  border-left: 5px solid $orange;
  border-bottom: 4px solid transparent;
  position: absolute;
  left: 0;
  top: calc(50% - 4px);
  transform: rotate(180deg);
  transition: all 300ms ease;
}

.home__main-slider_navigation--arrows .arrow-next {
  width: 54px;
  height: 24px;
  position: relative;  
  overflow: hidden;
  transition: all 200ms ease;  
  cursor: pointer;
}
.home__main-slider_navigation--arrows .arrow-next::before {
  content: "";
  display: block;
  width: 99%;
  height: 2px;
  background-color: $orange;
  position: absolute;
  left: 0;
  top: calc(50% - 1px);
  transition: all 300ms ease;
}
.home__main-slider_navigation--arrows .arrow-next::after {
  content: "";
  display: block;
  width: 0;
  height: 0;
  border-top: 4px solid transparent;
  border-left: 5px solid $orange;
  border-bottom: 4px solid transparent;
  position: absolute;
  right: 0;
  top: calc(50% - 4px);  
  transition: all 300ms ease;
}
.home__main-slider_navigation--arrows .swiper-button-disabled::before {
  background-color: #000;
}
.home__main-slider_navigation--arrows .swiper-button-disabled::after {
  border-left-color: #000;  
}
.home__main-slider_navigation--arrows .arrow-prev:hover:not(.swiper-button-disabled) {
  width: 38px;
  margin-left: -10px;
}
.home__main-slider_navigation--arrows .arrow-next:hover:not(.swiper-button-disabled) {
  width: 64px;
  margin-right: -10px;
}

