.orange-button {
  width: 316px;
  height: 62px;
  display: block;
  background-color: $orange;  
  font-family: Hind;
  font-size: 14px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #ffffff;
  position: relative;  
  transition: all 300ms ease;
    &.short {
      width: 200px;
    }
    &::before {
      content: "";
      display: block;
      width: 0%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 0;
      transition: all 300ms ease;
      background-color: $buttonHover;
    }
    &:hover::before {
      width: 100%;
    }
    & span {
      display: flex;
      align-items: center;
      width: 100%;
      height: 100%;      
      padding-left: 32px;  
      z-index: 1;
      position: relative;
        &::before {
          content: "";
          display: block;
          height: 1px;
          width: 54px;
          background-color: #fff;
          position: absolute;
          right: 36px;
          top: calc(50% - 1px);
          transition: all 300ms ease;        
        }
        &::after {
          content: "";
          display: block;
          width: 0;
          height: 0;
          border-top: 3.5px solid transparent;
          border-left: 4px solid #fff;
          border-bottom: 3.5px solid transparent;
          position: absolute;
          right: 32px;
          top: calc(50% - 4px);
          transition: all 300ms ease;        
        }
    }
    &.short span::before {
      width: 24px;
      right: 28px;
    }
    &.short span::after {      
      right: 26px;
    }
    &:hover span::before {      
      width: 64px;
      right: 26px; 
    }
    &:hover span::after {      
      right: 22px;
    }
    &.short:hover span::before {      
      width: 34px;
      right: 18px; 
    }
    &.short:hover span::after {      
      right: 16px;
    }
}
.dark-button {
  width: 316px;
  height: 62px;
  display: block;
  background-color: transparent;
  border: 1px solid $darkBorder;
  font-family: Hind;
  font-size: 14px;
