          transition: all 300ms ease;        
        }
        &::after {
          content: "";
          display: block;
          width: 0;
          height: 0;
          border-top: 3.5px solid transparent;
          border-left: 4px solid $h3Color;
          border-bottom: 3.5px solid transparent;
          position: absolute;
          right: 32px;
          top: calc(50% - 4px);
          transition: all 300ms ease;        
        }
    }
    &.short span::before {
      width: 24px;
      right: 28px;
    }
    &.short span::after {      
      right: 26px;
    }
    &:hover span::before {
      background-color: #fff;
      width: 64px;
      right: 26px; 
    }
    &:hover span::after {
      border-left-color: #fff;
      right: 22px;
    }
    &.short:hover span::before {      
      width: 34px;
      right: 18px; 
    }
    &.short:hover span::after {      
      right: 16px;
    }
}

